import React from "react";
import "./context-menu.scss";
import { useOutsideClick } from "../hooks/hooks";

type ButtonInfo = {
  label: string;
  func?: () => void;
};

type ContextMenuProps = {
  open: boolean;
  onClose: () => void;
  buttonGroups: ButtonInfo[][];
  right?: boolean;
};

const ContextMenu: React.FC<ContextMenuProps> = ({
  open,
  onClose,
  buttonGroups,
  right,
}) => {
  const ref = useOutsideClick(() => {
    onClose();
  });

  if (!open) return null;

  const buttons = buttonGroups.reduce<JSX.Element[]>((accum, current, idx) => {
    const items = current.map(item => {
      const clickHandler = () => {
        if (item.func) item.func();
        onClose();
      };
      return (
        <div
          onClick={clickHandler}
          className="context-menu-item"
          key={item.label + idx + "menuitem"}
        >
          {item.label}
        </div>
      );
    });
    const newAccum = [...accum, ...items];
    if (buttonGroups.length - 1 !== idx)
      newAccum.push(<div key={idx + "separator"} className="separator" />);
    return newAccum;
  }, []);

  let classes = "context-menu";
  if (right) classes += " right";
  return (
    <div ref={ref} className={classes}>
      {buttons}
    </div>
  );
};

export default ContextMenu;
