import { useEffect, useState } from "react";
import { getAnimals, setSelectedAnimal } from "../store/slices/animals-slice";
import { useAppDispatch, useAppSelector } from "../store/store";
import "./animals.scss";
import AnimalLightItem from "./animalLightItem";
import CreateAnimalModal from "../createAnimalModal/createAnimalModal";

const Animals = () => {
  const dispatch = useAppDispatch();
  const state = useAppSelector(state => state);
  const userId = state.authData.user?.id;
  const familyId = state.authData.user?.familyId;
  const { animals, selectedAnimal } = state.animalsData;
  const selectAnimal = (id: string) => {
    dispatch(setSelectedAnimal(id));
  };

  const [openModal, setOpenModal] = useState(false);

  useEffect(() => {
    if (animals?.length && !selectedAnimal) selectAnimal(animals[0].id);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [animals]);

  useEffect(() => {
    if (userId) {
      dispatch(getAnimals(userId));
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [familyId]);

  const animalsJsx = animals.map(item => {
    return (
      <AnimalLightItem
        key={item.id}
        handleClick={selectAnimal}
        animal={item}
        selected={selectedAnimal === item.id}
      />
    );
  });

  return (
    <div className="animals">
      <span>:</span>
      <div className="animals-list">{animalsJsx}</div>
      <div className="create-animal-wrapper">
        <button
          className="create-animal-button"
          onClick={() => {
            setOpenModal(true);
          }}
        >
          +
        </button>
        <CreateAnimalModal
          open={openModal}
          onClose={() => setOpenModal(false)}
        />
      </div>
    </div>
  );
};

export default Animals;
