import { useEffect } from "react";
import ActivityTools from "./activity-tools";
import HorizontalLegend from "../horizontalLegend/horizontalLegend";
import CalendarValues from "../calendarValues/calendarValues";
import { useAppDispatch, useAppSelector } from "../store/store";
import { loadActivity } from "../store/slices/profile-slice";
import { moveInterval } from "../store/slices/calendar-slice";

import "./activity.scss";

const Activity = () => {
  const dispatch = useAppDispatch();
  const { profileData, animalsData } = useAppSelector(state => state);
  const { eventsLoading } = profileData;
  const animalId = animalsData.selectedAnimal;

  useEffect(() => {
    if (animalId && !eventsLoading) dispatch(loadActivity);
  }, [animalId]);

  const handleWheel = (e: React.WheelEvent<HTMLTableElement>) => {
    if (e.deltaY < 0) dispatch(moveInterval(7));
    else dispatch(moveInterval(-7));
  };
  return (
    <div className="activity">
      <ActivityTools />
      <table onWheel={handleWheel} className="activity-calendar">
        <HorizontalLegend />
        <CalendarValues />
      </table>
    </div>
  );
};

export default Activity;
