import { DateTime } from "luxon";
import { useAppSelector } from "../store/store";

const HorizontalLegend = () => {
  const selectedInterval = useAppSelector(state => state.calendarData.interval);
  const horizontalLegendItems = [];
  for (let i = 0; i < selectedInterval.count("days"); i++) {
    const date = selectedInterval.start?.plus({ days: i });
    const isToday = date?.hasSame(DateTime.now(), "day");
    horizontalLegendItems.push(
      <td
        className={`${isToday ? "today-legend" : ""}`}
        key={date?.toFormat("dd.MM")}
      >
        <div className="month-legend">{date?.weekdayLong}</div>
        <div className="day-legend">{date?.toFormat("d")}</div>
      </td>
    );
  }
  return (
    <thead>
      <tr>
        <td className="vertical-legend"></td>
        {horizontalLegendItems}
      </tr>
    </thead>
  );
};

export default HorizontalLegend;
