import { PayloadAction, createSlice } from "@reduxjs/toolkit";
import { AppDispatch } from "../store";
import apiService, { LoginResult } from "../../utils/api-service";
import { RootState } from "../reducer";

export type User = {
  login: string;
  id: string;
  familyId: string;
};

type Auth = {
  user?: User;
  auth: boolean;
  loading: boolean;
  error?: string;
};

const initialState: Auth = {
  auth: false,
  loading: false,
};

const auth = createSlice({
  name: "auth",
  initialState,
  reducers: {
    setLoading(state) {
      state.loading = true;
    },
    setAuth(state, action: PayloadAction<LoginResult>) {
      state.error = undefined;
      state.loading = false;
      state.auth = true;
      state.user = action.payload.user;
      if (action.payload.accessToken)
        localStorage.setItem("accessToken", action.payload.accessToken);
    },
    setError(state, action: PayloadAction<string | undefined>) {
      state.loading = false;
      state.error = action.payload;
    },
    logOut(state) {
      state.auth = false;
      state.user = undefined;
      localStorage.setItem("accessToken", "");
    },
    setFamilyId(state, action: PayloadAction<string>) {
      if (state.user) {
        state.user.familyId = action.payload;
      }
    },
  },
});

export const { setLoading, setAuth, setError, logOut, setFamilyId } =
  auth.actions;

export const checkToken = (dispatch: AppDispatch) => {
  dispatch(setLoading());
  apiService
    .checkToken()
    .then(data => {
      dispatch(setAuth(data));
    })
    .catch(() => {
      dispatch(setError());
    });
};

export const registration =
  (login: string, password: string) => (dispatch: AppDispatch) => {
    apiService
      .registration(login, password)
      .then(data => {
        dispatch(setAuth(data));
      })
      .catch(e => dispatch(setError(e)));
  };

export const logIn =
  (login: string, password: string) => (dispatch: AppDispatch) => {
    apiService
      .login(login, password)
      .then(data => {
        dispatch(setAuth(data));
      })
      .catch(e => dispatch(setError(e)));
  };
export const changeFamily =
  (familyId: string) => (dispatch: AppDispatch, getState: () => RootState) => {
    const userId = getState().authData.user?.id;
    if (!userId) return;
    apiService.changeFamily(userId, familyId).then(data => {
      if (data) {
        dispatch(setFamilyId(familyId));
      }
    });
  };

export default auth.reducer;
