import { useEffect } from "react";
import { useAppDispatch, useAppSelector } from "../store/store";
import "./main-info.scss";
import { loadProfile } from "../store/slices/profile-slice";

const MainInfo = () => {
  const { profileData, animalsData } = useAppSelector(state => state);
  const dispatch = useAppDispatch();
  const profile = profileData.profile;
  const selectedAnimal = animalsData.selectedAnimal;

  useEffect(() => {
    if (selectedAnimal) {
      dispatch(loadProfile(selectedAnimal));
    }
  }, [selectedAnimal]);

  let ageString = "";

  const absFloor = (value: number) => {
    return Math.floor(Math.abs(value));
  };

  if (profile?.birthDate) {
    const diff = profile.birthDate.diffNow(["years", "months", "days"]);
    if (diff.years === 0) {
      ageString = `${absFloor(diff.months)} ${absFloor(diff.days)}`;
    } else {
      ageString = `${absFloor(diff.years)} ${absFloor(diff.months)}`;
    }
  }
  return (
    <div className="main-info">
      {!profileData.loading && !!profile && (
        <div className="main-info__table">
          <div>:</div>
          <div>{profile?.type}</div>
          <div>:</div>
          <div>{profile?.sex}</div>
          <div>:</div>
          <div>{ageString}</div>
          <div>():</div>
          <div>{profile?.weight}</div>
          <div>:</div>
          <div>{profile?.species}</div>
        </div>
      )}
    </div>
  );
};

export default MainInfo;
