import { useState } from "react";
import "./createAnimalModal.scss";
import { useAppDispatch } from "../store/store";
import { Profile } from "../store/slices/profile-slice";
import { DateTime } from "luxon";
import { createAnimal } from "../store/slices/animals-slice";
import { useOutsideClick } from "../hooks/hooks";

type CreateAnimalModalProps = {
  open: boolean;
  onClose: () => void;
};

const CreateAnimalModal: React.FC<CreateAnimalModalProps> = ({
  open,
  onClose,
}) => {
  const dispatch = useAppDispatch();
  const [name, setName] = useState("");
  const [type, setType] = useState("");
  const [sex, setSex] = useState("");
  const [birthDate, setBirthDate] = useState("");
  const [weight, setWeight] = useState("");
  const [species, setSpecies] = useState("");

  const ref = useOutsideClick(() => {
    onClose();
  });

  if (!open) return null;
  const createAnimalHandler = () => {
    if (!name || !type || !sex || !birthDate || !weight || !species)
      return alert("   ");
    const request: Profile = {
      name,
      type,
      sex,
      birthDate: DateTime.fromFormat(birthDate, "yyyy-MM-dd"),
      weight,
      species,
    };
    dispatch(createAnimal(request));
    setName("");
    setType("");
    setSex("");
    setBirthDate("");
    setWeight("");
    setSpecies("");
    onClose();
  };

  return (
    <div ref={ref} className="create-animal-modal">
      <input
        value={name}
        onChange={e => setName(e.target.value)}
        placeholder=""
        type="text"
      />
      <input
        value={type}
        onChange={e => setType(e.target.value)}
        placeholder="(,   )"
        type="text"
      />
      <input
        value={species}
        onChange={e => setSpecies(e.target.value)}
        placeholder=""
        type="text"
      />
      <select value={sex} onChange={e => setSex(e.target.value)}>
        <option value=""></option>
        <option value=""></option>
      </select>
      <label htmlFor="birthDate"> </label>
      <input
        value={birthDate}
        onChange={e => setBirthDate(e.target.value)}
        id="birthDate"
        type="date"
      />
      <label htmlFor="weigth">()</label>
      <input
        value={weight}
        onChange={e => setWeight(e.target.value)}
        id="weigth"
        type="number"
        step={0.5}
      />
      <button onClick={createAnimalHandler}></button>
    </div>
  );
};

export default CreateAnimalModal;
