import React, { useState } from "react";
import { User, changeFamily } from "../store/slices/auth-slice";
import ContextMenu from "../contextMenu/contextMenu";
import { useAppDispatch } from "../store/store";

type LoginProps = {
  user?: User;
};

const Login: React.FC<LoginProps> = ({ user }) => {
  const dispatch = useAppDispatch();
  const [openContextMenu, setOpenContextMenu] = useState(false);
  const copyToClipboard =
    (text: string = "", field: string) =>
    () => {
      if (text) {
        navigator.clipboard
          .writeText(text)
          .then(() => alert(`${field} `));
      }
    };
  const contextMenuHandler = (
    e: React.MouseEvent<HTMLDivElement, MouseEvent>
  ) => {
    e.preventDefault();
    setOpenContextMenu(true);
  };

  const joinFamily = () => {
    const familyId = prompt(" familyId", "");
    if (familyId) dispatch(changeFamily(familyId));
  };

  const buttons = [
    [
      { label: " userId", func: copyToClipboard(user?.id, "userId") },
      {
        label: " familyId",
        func: copyToClipboard(user?.familyId, "familyId"),
      },
    ],
    [{ label: "  ", func: joinFamily }],
  ];
  return (
    <div className="login-wrapper" onContextMenu={contextMenuHandler}>
      <div className="header-login">{user?.login}</div>
      <ContextMenu
        right
        open={openContextMenu}
        onClose={() => setOpenContextMenu(false)}
        buttonGroups={buttons}
      />
    </div>
  );
};

export default Login;
