import { useState } from "react";
import { logIn, registration } from "../store/slices/auth-slice";
import { useAppDispatch, useAppSelector } from "../store/store";

import "./authPage.scss";
const AuthPage = () => {
  const error = useAppSelector(state => state.authData.error);
  const dispatch = useAppDispatch();
  const [login, setLogin] = useState("");
  const [password, setPassword] = useState("");

  const registrationHandler = () => {
    if (!login || !password) return alert("   ");
    dispatch(registration(login, password));
  };

  const loginHandler = () => {
    if (!login || !password) return alert("   ");
    dispatch(logIn(login, password));
  };

  return (
    <div className="auth-page">
      <label htmlFor="login"></label>
      <input
        value={login}
        onChange={e => setLogin(e.target.value)}
        id="login"
        type="text"
      />
      <label htmlFor="password"></label>
      <input
        value={password}
        onChange={e => setPassword(e.target.value)}
        id="password"
        type="password"
      />
      <div className="auth-page__buttons">
        <button onClick={loginHandler}></button>
        <button onClick={registrationHandler}></button>
      </div>
      {error && <div className="auth-page-error">{error}</div>}
    </div>
  );
};

export default AuthPage;
