import React, { useRef, useState } from "react";
import ContextMenu from "../contextMenu/contextMenu";
import "./event.scss";
import { useAppDispatch } from "../store/store";
import {
  DailyEvent,
  checkDone,
  deleteEvent,
  deleteGroup,
} from "../store/slices/profile-slice";

type EventProp = {
  event: DailyEvent;
};

const Event: React.FC<EventProp> = ({ event }) => {
  const dispatch = useAppDispatch();
  const ref = useRef<HTMLDivElement>(null);
  const [openContextMenu, setOpenContextMenu] = useState(false);

  const contextMenuHandler = (
    e: React.MouseEvent<HTMLDivElement, MouseEvent>
  ) => {
    e.preventDefault();
    setOpenContextMenu(true);
  };

  const deleteEventFunc = () => {
    dispatch(deleteEvent(event.id));
  };

  const deleteGroupFunc = () => {
    dispatch(deleteGroup(event.groupId));
  };

  const checkDoneFunc = () => {
    dispatch(checkDone(event.id));
  };

  const buttons = [
    [
      { label: " ", func: deleteEventFunc },
      { label: " ", func: deleteGroupFunc },
    ],
  ];
  if (!event.done) buttons.push([{ label: "", func: checkDoneFunc }]);

  let eventClassName = "event";
  if (event.done) eventClassName += " done";
  return (
    <div className="event-wrapper">
      <div
        className={eventClassName}
        ref={ref}
        onContextMenu={contextMenuHandler}
      >
        {event.name}
      </div>
      <ContextMenu
        open={openContextMenu}
        onClose={() => setOpenContextMenu(false)}
        buttonGroups={buttons}
      />
    </div>
  );
};

export default Event;
