import { PayloadAction, createSlice } from "@reduxjs/toolkit";
import { AppDispatch } from "../store";
import apiService, { CreateActivityProps } from "../../utils/api-service";
import { DateTime } from "luxon";
import { RootState } from "../reducer";

export type DailyEvent = {
  name: string;
  date: DateTime;
  id: string;
  groupId: string;
  type: string;
  done?: boolean;
};

export type Profile = {
  name: string;
  type: string;
  sex: string;
  weight: string;
  species: string;
  birthDate: DateTime;
};

type ProfileState = {
  profile?: Profile;
  loading: boolean;
  error: boolean;
  eventsLoading: boolean;
  events: DailyEvent[];
};

const initialState: ProfileState = {
  profile: undefined,
  loading: false,
  error: false,
  eventsLoading: false,
  events: [],
};

const profile = createSlice({
  name: "profile",
  initialState,
  reducers: {
    startLoading(state) {
      state.loading = true;
    },
    setProfile(state, action: PayloadAction<Profile>) {
      state.loading = false;
      state.error = false;
      state.profile = action.payload;
    },
    setError(state) {
      state.loading = false;
      state.error = true;
      state.profile = undefined;
    },
    setEvents(state, action: PayloadAction<DailyEvent[]>) {
      state.events = action.payload;
      state.eventsLoading = false;
    },
    startLoadEvents(state) {
      state.eventsLoading = true;
    },
  },
});

const { startLoading, setProfile, setError, setEvents, startLoadEvents } =
  profile.actions;

export const loadProfile = (id: string) => (dispatch: AppDispatch) => {
  dispatch(startLoading());
  apiService
    .loadProfile(id)
    .then(data => {
      dispatch(setProfile(data));
    })
    .catch(() => dispatch(setError()));
};

export const createActivity =
  (input: CreateActivityProps) => (dispatch: AppDispatch) => {
    apiService.createActivity(input).then(data => {
      if (data) dispatch(loadActivity);
    });
  };

export const deleteEvent = (id: string) => (dispatch: AppDispatch) => {
  apiService.deleteEvent(id).then(data => {
    if (data) dispatch(loadActivity);
  });
};

export const deleteGroup = (id: string) => (dispatch: AppDispatch) => {
  apiService.deleteGroup(id).then(data => {
    if (data) dispatch(loadActivity);
  });
};

export const checkDone = (id: string) => (dispatch: AppDispatch) => {
  apiService.checkDone(id).then(data => {
    if (data) dispatch(loadActivity);
  });
};

export const loadActivity = (
  dispatch: AppDispatch,
  getState: () => RootState
) => {
  const state = getState();
  const animalId = state.animalsData.selectedAnimal;
  if (!animalId) return;
  dispatch(startLoadEvents());
  apiService.loadEvents(animalId, state.calendarData.interval).then(data => {
    dispatch(setEvents(data));
  });
};

export default profile.reducer;
