import axios, { AxiosError } from "axios";
import { DailyEvent, Profile } from "../store/slices/profile-slice";
import { DateTime, Interval } from "luxon";
import { User } from "../store/slices/auth-slice";
import { AnimalLight } from "../store/slices/animals-slice";

const baseUrl =
  process.env.NODE_ENV === "development"
    ? "http://localhost:8888/"
    : "https://takecareofthedog-vbl.amvera.io/";

type DailyEventResponse = {
  name: string;
  date: string;
  id: string;
  groupId: string;
  type: string;
};

export type LoginResult = {
  user: User;
  accessToken: string;
};

export type CreateActivityProps = {
  selectedType: string;
  eventName: string;
  startDate: string;
  dayCount?: string;
  timePerDay?: string[];
  unitCount?: string;
  selectedUnit?: string;
  weekday?: string;
  animalId: string;
};

export interface CreateAnimalRequest extends Profile {
  userId: string;
}

interface ProfileResponse extends Omit<Profile, "dailyEvents" | "birthDate"> {
  birthDate: string;
}

const mapDailyEvent = (item: DailyEventResponse) => {
  return {
    ...item,
    date: DateTime.fromISO(item.date),
  } as DailyEvent;
};

const apiService = {
  loadProfile: (id: string) => {
    return axios
      .get<ProfileResponse>(`${baseUrl}profile`, { params: { id } })
      .then(result => {
        return {
          ...result.data,
          birthDate: DateTime.fromISO(result.data.birthDate),
          dailyEvents: [],
        } as Profile;
      });
  },

  createActivity: (input: CreateActivityProps) => {
    const luxonStartDate = input.startDate
      ? DateTime.fromFormat(input.startDate, "yyyy-MM-dd")
      : null;
    return axios
      .post<DailyEventResponse[]>(`${baseUrl}createActivity`, {
        ...input,
        startDate: luxonStartDate?.toUTC(),
      })
      .then(() => true)
      .catch(() => false);
  },

  loadEvents: (animalId: string, interval: Interval) => {
    return axios
      .get<DailyEventResponse[]>(`${baseUrl}events`, {
        params: {
          startDate: interval.start?.startOf("day").toISO(),
          endDate: interval.end?.endOf("day").toISO(),
          animalId,
        },
      })
      .then(result => result.data.map(mapDailyEvent));
  },

  deleteEvent: (id: string) => {
    return axios
      .post(`${baseUrl}deleteEvent`, {
        id,
      })
      .then(() => true)
      .catch(() => false);
  },

  deleteGroup: (id: string) => {
    return axios
      .post(`${baseUrl}deleteGroup`, {
        id,
      })
      .then(() => true)
      .catch(() => false);
  },

  checkDone: (id: string) => {
    return axios
      .post(`${baseUrl}done`, {
        id,
      })
      .then(() => true)
      .catch(() => false);
  },

  registration: (login: string, password: string) => {
    return axios
      .post<LoginResult>(`${baseUrl}registration`, { login, password })
      .then(data => data.data)
      .catch((e: AxiosError) => {
        throw e.request?.responseText;
      });
  },

  login: (login: string, password: string) => {
    return axios
      .post<LoginResult>(`${baseUrl}login`, { login, password })
      .then(data => data.data)
      .catch((e: AxiosError) => {
        throw e.request?.responseText;
      });
  },

  checkToken: () => {
    const token = localStorage.getItem("accessToken");
    if (!token) return Promise.reject(false);
    return axios
      .post<LoginResult>(`${baseUrl}`, { token })
      .then(data => data.data)
      .catch((e: AxiosError) => {
        throw e.request?.responseText;
      });
  },

  createAnimal: (input: CreateAnimalRequest) => {
    return axios
      .post<AnimalLight[]>(`${baseUrl}createAnimal`, {
        ...input,
        birthDate: input.birthDate.toISO(),
      })
      .then(data => data.data)
      .catch((e: AxiosError) => {
        throw e.request?.responseText;
      });
  },

  loadAnimals: (id: string) => {
    return axios
      .get<AnimalLight[]>(`${baseUrl}animals`, { params: { id } })
      .then(result => result.data);
  },

  deleteAnimal: (id: string) => {
    return axios
      .post(`${baseUrl}deleteAnimal`, {
        id,
      })
      .then(() => true)
      .catch(() => false);
  },

  changeFamily: (userId: string, familyId: string) => {
    return axios
      .post(`${baseUrl}changeFamily`, {
        userId,
        familyId,
      })
      .then(() => true)
      .catch(() => false);
  },
};

export default apiService;
