import Event from "../event/event";
import { DailyEvent } from "../store/slices/profile-slice";

type RowValueProps = {
  verticalLabel: string;
  columns: DailyEvent[][];
  rowBorder: boolean;
  columnBorder?: number;
};

const RowValue: React.FC<RowValueProps> = ({
  verticalLabel,
  columns,
  columnBorder,
  rowBorder,
}) => {
  const cols = columns.map((columnInfo, rowIdx) => {
    return (
      <td
        className={`${rowIdx === columnBorder ? "column-border" : ""}`}
        key={rowIdx + "column"}
      >
        <div className="timeslot">
          {columnInfo.map(event => {
            return <Event key={event.id} event={event} />;
          })}
        </div>
      </td>
    );
  });
  return (
    <tr className={`${rowBorder ? "row-border" : ""}`}>
      <td className="vertical-legend">{verticalLabel}</td>
      {cols}
    </tr>
  );
};

export default RowValue;
