import { PayloadAction, createSlice } from "@reduxjs/toolkit";
import { AppDispatch } from "../store";
import { RootState } from "../reducer";
import apiService, { CreateAnimalRequest } from "../../utils/api-service";
import { Profile } from "./profile-slice";

export type AnimalLight = {
  id: string;
  name: string;
};

type AnimalsState = {
  animals: AnimalLight[];
  selectedAnimal?: string;
};

const initialState: AnimalsState = {
  animals: [],
};

const animals = createSlice({
  name: "animals",
  initialState,
  reducers: {
    setAnimals(state, action: PayloadAction<AnimalLight[]>) {
      if (state) {
        state.animals = action.payload;
      }
    },
    setSelectedAnimal(state, action: PayloadAction<string | undefined>) {
      state.selectedAnimal = action.payload;
    },
  },
});

export const { setAnimals, setSelectedAnimal } = animals.actions;

export const createAnimal =
  (profile: Profile) => (dispatch: AppDispatch, getState: () => RootState) => {
    const userId = getState().authData.user?.id;
    if (!userId) return;
    const createAnimalRequest: CreateAnimalRequest = { ...profile, userId };
    apiService
      .createAnimal(createAnimalRequest)
      .then(data => {
        dispatch(setAnimals(data));
      })
      .catch(e => console.log(e));
  };

export const getAnimals =
  (id?: string) => (dispatch: AppDispatch, getState: () => RootState) => {
    const userId = getState().authData.user?.id || id;
    if (!userId) return;
    apiService
      .loadAnimals(userId)
      .then(data => {
        dispatch(setAnimals(data));
      })
      .catch(e => console.log(e));
  };
export const deleteAnimal = (id: string) => (dispatch: AppDispatch) => {
  apiService.deleteAnimal(id).then(data => {
    if (data) {
      dispatch(getAnimals());
    }
  });
};

export default animals.reducer;
