import { PayloadAction, createSlice } from "@reduxjs/toolkit";
import utils from "../../utils/utills";
import { Interval } from "luxon";
import { AppDispatch } from "../store";
import { RootState } from "../reducer";
import { loadActivity } from "./profile-slice";

const luxonNow = utils.getDateLuxonNow();
const initInterval = Interval.fromDateTimes(
  luxonNow.startOf("week"),
  luxonNow.endOf("week")
);

const initialState = {
  interval: initInterval,
};

const calendar = createSlice({
  name: "calendar",
  initialState,
  reducers: {
    setInterval(state, action: PayloadAction<number>) {
      const newStart = state.interval.start?.plus({ days: action.payload });
      const newEnd = state.interval.end?.plus({ days: action.payload });
      if (newStart && newEnd) {
        state.interval = Interval.fromDateTimes(newStart, newEnd);
      }
    },
  },
});

export const { setInterval } = calendar.actions;

export const moveInterval =
  (unitCount: number) => (dispatch: AppDispatch, getState: () => RootState) => {
    dispatch(setInterval(unitCount));
    dispatch(loadActivity);
  };

export default calendar.reducer;
