import { useEffect, useState } from "react";
import RowValue from "../rowValue/rowValue";
import { useAppSelector } from "../store/store";
import { DateTime } from "luxon";
import { DailyEvent } from "../store/slices/profile-slice";

export type CurrentDayBorder = {
  row?: number;
  column?: number;
};

type ColumnsInfo = {
  columns: DailyEvent[][];
  verticalLabel: string;
};

const CalendarValues = () => {
  const {
    calendarData: { interval },
    profileData: { events },
  } = useAppSelector(state => state);

  const [currentDayBorder, setCurrentDayBorder] = useState<CurrentDayBorder>(
    {}
  );
  useEffect(() => {
    if (interval) {
      let column;
      let row;
      for (let dayIdx = 0; dayIdx < interval.count("days"); dayIdx++) {
        const date = interval.start?.plus({ days: dayIdx });
        if (date?.hasSame(DateTime.now(), "day")) {
          column = dayIdx;
        }
      }
      row = DateTime.now().get("hour") + 1;
      setCurrentDayBorder({ column, row });
    }
  }, [interval]);

  const columnsInRowInfo: ColumnsInfo[] = [];

  const regularEvents = events.filter(
    item => item.type === "regular" || item.type === "weekly"
  );
  columnsInRowInfo.push({ columns: [], verticalLabel: "" });
  for (let dayIdx = 0; dayIdx < interval.count("days"); dayIdx++) {
    const date = interval.start?.plus({ days: dayIdx });
    const timeslotEvents: DailyEvent[] = [];
    if (date) {
      const currentEvents =
        regularEvents.filter(event => event.date.hasSame(date, "day")) || [];
      currentEvents.forEach(item => {
        timeslotEvents.push(item);
      });
    }
    columnsInRowInfo[0].columns.push(timeslotEvents);
  }

  const dayliEvents = events.filter(item => item.type === "daily");

  for (let hour = 0; hour <= 23; hour++) {
    columnsInRowInfo.push({ columns: [], verticalLabel: String(hour) });
    for (let dayIdx = 0; dayIdx < interval.count("days"); dayIdx++) {
      const date = interval.start?.plus({ days: dayIdx }).set({ hour });
      const timeslotEvents: DailyEvent[] = [];
      if (date) {
        const currentEvents =
          dayliEvents.filter(event => event.date.hasSame(date, "hour")) || [];
        currentEvents.forEach(item => {
          timeslotEvents.push(item);
        });
      }
      columnsInRowInfo[hour + 1].columns.push(timeslotEvents);
    }
  }
  const rows = columnsInRowInfo.map((item, idx) => {
    return (
      <RowValue
        rowBorder={idx === currentDayBorder.row}
        key={item.verticalLabel + "hour"}
        verticalLabel={String(item.verticalLabel)}
        columns={item.columns}
        columnBorder={currentDayBorder.column}
      />
    );
  });
  return <tbody>{rows}</tbody>;
};

export default CalendarValues;
