import { moveInterval } from "../store/slices/calendar-slice";
import { useAppDispatch, useAppSelector } from "../store/store";

const ActivityTools = () => {
  const selectedInterval = useAppSelector(state => state.calendarData.interval);
  const dispatch = useAppDispatch();
  const stringInterval = `${selectedInterval.start?.toFormat(
    "d MMMM"
  )} - ${selectedInterval.end?.toFormat("d MMMM")}`;

  const nextPeriod = () => {
    dispatch(moveInterval(7));
  };

  const prevPeriod = () => {
    dispatch(moveInterval(-7));
  };

  return (
    <div className="activity__tools">
      <div className="interval-controller">
        <span onClick={prevPeriod} className="interval-controller__button">
          &#8592;
        </span>
        <div>{stringInterval}</div>
        <span onClick={nextPeriod} className="interval-controller__button">
          &#8594;
        </span>
      </div>
    </div>
  );
};

export default ActivityTools;
