import { useState } from "react";
import { useAppDispatch, useAppSelector } from "../store/store";
import { createActivity } from "../store/slices/profile-slice";
import { CreateActivityProps } from "../utils/api-service";
import { Info } from "luxon";
import utils from "../utils/utills";

import "./create-activity-form.scss";

const CreateActivityForm = () => {
  const dispatch = useAppDispatch();
  const animalId = useAppSelector(state => state.animalsData.selectedAnimal);
  const types = [
    { label: "", value: "regular" },
    { label: "", value: "daily" },
    { label: "", value: "weekly" },
  ];
  const units = [
    { label: "", value: "day" },
    { label: "", value: "month" },
  ];
  const typesItems = types.map(item => (
    <option key={item.value} value={item.value}>
      {item.label}
    </option>
  ));
  const unitItems = units.map(item => (
    <option key={item.value} value={item.value}>
      {item.label}
    </option>
  ));
  const timesList = [<option key="" value=""></option>];
  for (let i = 0; i < 24; i++) {
    const hour = i < 10 ? `0${i}` : i;
    const v1 = `${hour}:00`;
    timesList.push(
      <option value={v1} key={v1}>
        {v1}
      </option>
    );
  }
  const [selectedType, setSelectedType] = useState(types[0].value);
  const [eventName, setEventName] = useState("");
  const [startDate, setStartDate] = useState("");
  const [selectedUnit, setSelectedUnit] = useState(units[0].value);
  const [unitCount, setUnitCount] = useState("1");
  const [timePerDay, setTimePerDay] = useState([""]);
  const [dayCount, setDayCount] = useState("1");
  const [weekday, setWeekday] = useState("0");

  const newTimePerDay = () => {
    setTimePerDay(oldValue => {
      return [...oldValue, ""];
    });
  };

  const deleteTimePerDay = (idx: number) => {
    setTimePerDay(oldValue => {
      return oldValue.filter((item, i) => i !== idx);
    });
  };

  const changeTimePerDay = (value: string, idx: number) => {
    setTimePerDay(oldValue => {
      const newValue = [...oldValue];
      newValue[idx] = value;
      return newValue;
    });
  };
  const setInitValues = () => {
    setEventName("");
    setStartDate("");
    setSelectedUnit(units[0].value);
    setUnitCount("1");
    setTimePerDay([""]);
    setDayCount("1");
    setWeekday("0");
  };
  const createAct = () => {
    if (!animalId) return alert("  ");
    let request: CreateActivityProps;
    switch (selectedType) {
      case "daily":
        if (
          !selectedType ||
          !eventName ||
          !startDate ||
          !dayCount ||
          timePerDay.some(item => !item)
        )
          return alert("   ");
        request = {
          selectedType,
          eventName,
          startDate,
          dayCount,
          timePerDay,
          animalId,
        };
        dispatch(createActivity(request));
        setInitValues();
        break;
      case "regular":
        if (
          !selectedType ||
          !eventName ||
          !startDate ||
          !unitCount ||
          !selectedUnit
        )
          return alert("   ");
        request = {
          selectedType,
          eventName,
          startDate,
          unitCount,
          selectedUnit,
          animalId,
        } as CreateActivityProps;
        dispatch(createActivity(request));
        setInitValues();
        break;
      case "weekly":
        if (!selectedType || !eventName || !weekday)
          return alert("   ");
        request = {
          selectedType,
          eventName,
          weekday,
          animalId,
        } as CreateActivityProps;
        dispatch(createActivity(request));
        setInitValues();
        break;
      default:
        break;
    }
  };

  const timePerDayItems = timePerDay.map((item, idx) => {
    return (
      <div key={item + idx}>
        <select
          className="flex-grow"
          value={item}
          onChange={e => changeTimePerDay(e.target.value, idx)}
        >
          {timesList}
        </select>
        <button onClick={() => deleteTimePerDay(idx)} className="small-button">
          -
        </button>
      </div>
    );
  });

  const weekdays = Info.weekdays().map((item, idx) => {
    return (
      <option className="capitalize" key={item} value={idx}>
        {utils.capitalizeFirstLetter(item)}
      </option>
    );
  });

  return (
    <div className="create-activity-form">
      <select
        value={selectedType}
        onChange={e => setSelectedType(e.target.value)}
      >
        {typesItems}
      </select>

      <input
        value={eventName}
        onChange={e => setEventName(e.target.value)}
        placeholder=" "
        type="text"
      />
      {selectedType !== "weekly" && (
        <>
          <label htmlFor="startDate"> </label>
          <input
            value={startDate}
            onChange={e => setStartDate(e.target.value)}
            id="startDate"
            type="date"
          />
        </>
      )}
      {selectedType === "regular" && (
        <>
          <label></label>
          <div>
            <input
              onChange={e => setUnitCount(e.target.value)}
              min="1"
              type="number"
              value={unitCount}
            />
            <select
              value={selectedUnit}
              onChange={e => setSelectedUnit(e.target.value)}
              className="flex-grow"
            >
              {unitItems}
            </select>
          </div>
        </>
      )}

      {selectedType === "daily" && (
        <>
          <label htmlFor="dayCount"></label>
          <input
            value={dayCount}
            onChange={e => setDayCount(e.target.value)}
            id="dayCount"
            type="number"
            min="1"
          />
          <div>
            <label> </label>
            <button className="small-button" onClick={newTimePerDay}>
              +
            </button>
          </div>
          {timePerDayItems}
        </>
      )}
      {selectedType === "weekly" && (
        <>
          <label> </label>
          <select value={weekday} onChange={e => setWeekday(e.target.value)}>
            {weekdays}
          </select>
        </>
      )}
      <button onClick={createAct}></button>
    </div>
  );
};

export default CreateActivityForm;
