import React, { useState } from "react";
import { AnimalLight, deleteAnimal } from "../store/slices/animals-slice";
import ContextMenu from "../contextMenu/contextMenu";
import { useAppDispatch } from "../store/store";

type AnimalLightProps = {
  animal: AnimalLight;
  selected: boolean;
  handleClick: (id: string) => void;
};

const AnimalLightItem: React.FC<AnimalLightProps> = ({
  animal,
  selected,
  handleClick,
}) => {
  const [openContextMenu, setOpenContextMenu] = useState(false);
  const dispatch = useAppDispatch();
  const contextMenuHandler = (
    e: React.MouseEvent<HTMLDivElement, MouseEvent>
  ) => {
    e.preventDefault();
    setOpenContextMenu(true);
  };
  let animalClassName = "animal-light";
  if (selected) animalClassName += " selected";
  const buttons = [
    [
      {
        label: "",
        func: () => {
          dispatch(deleteAnimal(animal.id));
        },
      },
    ],
  ];
  return (
    <div className="animal-wrapper">
      <div
        className={animalClassName}
        onClick={() => handleClick(animal.id)}
        key={animal.id}
        onContextMenu={contextMenuHandler}
      >
        {animal.name}
      </div>
      <ContextMenu
        open={openContextMenu}
        onClose={() => setOpenContextMenu(false)}
        buttonGroups={buttons}
      />
    </div>
  );
};

export default AnimalLightItem;
